.card {
  padding: 0.5rem;
  max-width: 50rem;
}

.list {
  list-style: none;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}

/* media query for desktop */
@media (min-width: 768px) {
  .card {
    padding: 2rem;
  }
  .list {
    flex-direction: row;
  }
}
